import React, {Component, Fragment} from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import {
    Container, Row, Col
} from 'reactstrap';

class Posts extends Component{

    state = { posts: [] }

    getPost(path) {
        axios.get(path)
            .then(res => this.setState(prev => ({posts: [...prev.posts, res.data]})));
    }

    componentDidMount() {
        axios.get('./post-data/posts.json')
            .then(response => response.data.map((value) => (
                this.getPost('./post-data/' + value + '.json')
            )));
    }

    render(){
        return(
            <Fragment>
                {this.state.posts ? (<section style={{ background: '#f4f4f4' }}>
                    <Container>
                        <Row className="pt-4">
                        {Object.values(this.state.posts).map((data, key) => {
                            return <Col lg={{ size: 4 }} sm={{ size: 6 }} xs={{ size: 6 }} className="mb-4" key={key}>
                                <div className="card w-100">
                                    <div className="card-body">
                                        <h3 className="card-title">{data.title}</h3>
                                        <p className="text-muted">{data.time}</p>
                                        <p>{data.description}</p>
                                    </div>
                                    <Link className="stretched-link" to={process.env.PUBLIC_URL + '/post/' + data.id}></Link>
                                </div>
                                </Col>
                            }
                        )}
                        </Row>
                    </Container>
                </section>) : (<p>No posts found!</p>)}
            </Fragment>
        )
    }
}

export default Posts;