import React, { Fragment } from 'react';
import {
  HashRouter, Route, Switch
} from 'react-router-dom';
import Header from './components/common/Header';
import Footer from './components/common/Footer';
import HomePage from './components/pages/HomePage';
import BlogPage from './components/pages/BlogPage';
import ContactPage from './components/pages/ContactPage';
import AboutPage from './components/pages/AboutPage';
import NotFoundPage from './components/pages/NotFoundPage';
import Post from './components/posts/Post';

function App() {
  return (
    <Fragment>
      <HashRouter basename={process.env.PUBLIC_URL + '/'}>
        <Header />
        <div style={{ minHeight: "85vh" }}>
          <Switch>
            <Route path={process.env.PUBLIC_URL + '/'} component={HomePage} exact={true} />
            <Route path={process.env.PUBLIC_URL + '/blog'} component={BlogPage} exact={true} />
            <Route path={process.env.PUBLIC_URL + '/contact'} component={ContactPage} exact={true} />
            <Route path={process.env.PUBLIC_URL + '/about'} component={AboutPage} exact={true} />
            <Route path={process.env.PUBLIC_URL + '/post/:id'} component={Post} exact={true} />
            <Route component={NotFoundPage} />
          </Switch>
        </div>
        <Footer />
      </HashRouter>
    </Fragment>
  );
}

export default App;
